
.Header{height:300px;position:relative
}
.Header .banner{background:url(../assets/banner1.jpg) center #000;position:absolute;top:0;left:0;width:100%;height:100%;background-size:cover
}
.Header .header-outer{height:100%;position:relative;overflow:hidden
}
.Header .header-outer .main-nav{float:left;margin:20px
}
.Header .header-outer .main-nav a{color:#fff;opacity:0.6;text-decoration:none;text-shadow:0 1px rgba(0,0,0,0.2)
}
.Header .header-outer .main-nav a:hover{opacity:1
}



